<template>
    <div class="myPlaylist">
        <div class="bgTop">
            <img :src="lisObj.pUrl"/>
            <div class="mebox"></div>
        </div>
       
         <van-nav-bar :border="false">
             <template #left>
                 <div>
                    <span class="iconfont icon-arrow-fine-up-copy" @click.stop="goBack"></span>
                    <span class="icon-shangbiao iconfont">歌单</span>
                 </div>
             </template>
             <template #right>
                    <div>
                        <span class="iconfont icon-fangda" @click="goPack('Seek')"></span>
                        <span class="iconfont icon-sandian"></span>
                    </div>
             </template>
         </van-nav-bar>

         <!-- 歌单头部 -->
        <div class="PlayTile">
                <img :src="lisObj.pUrl"/>
                <div class="tet">
                    <p>{{lisObj.pName}}</p>
                    <p>
                        <img :src="lisObj.pAutoUrl"/>
                        <span class="icon-arrow-right iconfont">{{lisObj.pAuto}}</span>
                    </p>
                    <p>{{lisObj.pBrief}}</p>
                </div>
        </div>

        <!-- 功能键 -->
        <div class="buts">
            <span class="iconfont" :class="item.icon" v-for="(item,index) in dataButs" :key="index">
                {{item.txet}}
            </span> 
        </div>
        
        <!-- 歌单列表 -->
        <div class="details">
            <div class="herads">
                <span class="iconfont icon-bofangjian1" @click="ff_queue"></span>
                <span class="" >播放全部<span>(共{{newList.length}}首)</span></span>
                <span class="iconfont icon-jiahao">收藏 ({{numren}})</span>
            </div>
            <div class="bodys">
                <ul>
                    <li class="danSong" v-for="(item,index) in newList" :key="index" @click="ff_songs(item.songId,item.songName,item.songUrl,item.autName,item.songBrief,true)">
                        <span>{{index-0+1}}</span>
                        <div class="gequ">
                            <van-skeleton title row-width=100%  :row="2" :loading="loading">
                                    <p>{{item.songName}}</p>
                                    <p>
                                        <span>独家</span>
                                        <span>{{item.autName}}</span>
                                        <span>-{{item.songBrief}}</span>
                                    </p>
                            </van-skeleton>
                           
                        </div>
                        <span class="iconfont icon-sandian"></span>
                    </li>
                </ul>
            </div>
        </div>


      

    </div>
</template>

<script>
import {mapState} from 'vuex'
import Flash from '../components/Flash.vue'
import '../assets/less/Playlist.less'
    export default {
        name : 'Playlist',
        data(){
            return{
                objList  : {},
                dataButs : [
                    {icon:'icon-xinxi',txet:'评论'},
                    {icon:'icon-fenxiang',txet:'分享'},
                    {icon:'icon-xiazai',txet:'下载'},
                    {icon:'icon-duoxuan',txet:'多选'},
                ],
                listObj    : {},
                singleList : [],
                musicObj   : {},
                PlaylistId : null,

                newList    : [],
                oldList    : [],
                lisObj     : {},
                // 状态有三种 1数据为null 2歌单不符 3直接拿缓存的
                status     : 1,
                numren     : 0,
                loading    : true,

            }
        },
        created(){
           
             
             this.numren = Math.floor(Math.random()*(10000-1000)+1000);
             
              this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
              });
               setTimeout(()=>{
                    this.loading = false;
                },500)


              this.PlaylistId = this.$route.query.id;
              //  先获取判断缓存的歌单
              let nowList = JSON.parse(sessionStorage.getItem('listStr'));

              if(nowList == null){
                   this.status = 1;
                   console.log('重新缓存1');
                   //   初始化歌单
                   this.ff_init(this.$route.query.id); 
                   
                 
                   this.newList.shift();
                   
                    

              }else{
                     if(Number(nowList[0]['pId']) != this.PlaylistId){
                        console.log('重新缓存2');
                        this.status = 2;
                        //   初始化歌单
                        this.ff_init(this.$route.query.id); 
                         
                       
                    }else{
                        this.status = 3;
                        console.log("拿之前存储的数据")
                        this.$toast.clear();
                        this.lisObj = nowList[0];
                    
                        nowList.shift();
                        console.log(nowList)
                        nowList.forEach((Item,index)=>{
                                this.newList.push(Item);
                        })
                       
                    }
              }
              
              
              
         
        },
        watch: {
            oldList(news,old){
               
                if(this.status == 1 || this.status == 2){
                    this.lisObj = news[0];
                }
                
                // 队列存储
                if(news.length >= this.singleList.length){
                              
                              this.newList = news.slice(1,news.length);
                        //  第一次请求
                            if(this.status == 1){
                                      sessionStorage.setItem('listStr',JSON.stringify(news));
                                      this.newList = news.slice(1,news.length);
                        //  第二次请求
                            }else if(this.status == 2){
                                    sessionStorage.setItem('listStr',JSON.stringify(news));
                            }

                        
                }
            }
        },
        methods:{
          
            // 初始化歌单
            ff_init(id){
                 this.axios({
                     url:'/playlist/detail',
                     method : 'GET',
                     params : {
                         id 
                     }
                 })
                 .then(res=>{
                     if(res.data.code == 200){
                        
                         
                        
                         this.objList = res.data.playlist;
                         console.log('这',this.objList)

                         this.listObj = this.objList.creator;

                         this.objList.trackIds.forEach(item=>{
                            this.singleList.push(item)
                         });


                       
                      
                        console.log('111')
                        // 缓存歌单的信息 为数组的第一项
                        this.oldList.push({
                            //  歌单id
                             pId      : this.PlaylistId,
                            //  歌单封面
                             pUrl     : this.objList.coverImgUrl,
                            //  歌单
                             pName    : this.objList.name,
                            //  歌单作者
                             pAuto    : this.objList.creator.nickname,
                            //  作者头像
                             pAutoUrl : this.objList.creator.backgroundUrl,
                            //  歌单简介
                             pBrief   : this.objList.description
                         })
                      

                        // 获取歌单中的歌曲详情
                        this.singleList.forEach(item=>{
                            this.ff_danSong(item.id);
                            
                        })
                        
                         this.$toast.clear();
                     }
                    
                 })
                 .catch(err=>{
                     console.log(err)
                 })
            },
            // 返回上一页
            goBack(){
                this.$router.go(-1);
            },
            // 歌曲详情
            ff_danSong(ids){
                this.axios({
                    url : '/song/detail',
                    method : 'GET',
                    params : {
                        ids
                    }
                })
                .then(res=>{
                    if(res.data.code == 200){
                        // this.newList.push(res.data);
                
                        this.oldList.push({
                             //  歌名 
                             songName  : res.data.songs[0].name,
                             //  歌id
                             songId    : res.data.songs[0].id,
                             //  歌曲封面图片
                             songUrl   : res.data.songs[0].al.picUrl,
                             //  歌手 
                             autName   : res.data.songs[0].ar[0].name,
                             //  歌曲简介
                             songBrief : res.data.songs[0].al.name
                        })
                        console.log('this.oldList',this.oldList)
                        
                    }     
                })             
                .catch(err=>{
                    console.log(err)
                })
            },
            // 获取歌曲url
            ff_songs(id,name,imgUrl,auto,Brief,isSingle=false){

                
                if(this.songTime){
                    clearTimeout(this.songTime)
                }
                this.songTime = setTimeout(()=>{

                     this.axios({
                     url : '/song/url',
                     method : 'GET',
                     params : {
                         id
                     }
                 })
                 .then(res=>{
                     console.log(res)
                     if(res.data.code == 200){
                           let a = {};
                          
                           a.name     = name;
                           a.url      = res.data.data[0].url;
                           a.imgUrl   = imgUrl;
                           a.id       = id;
                           a.isFlash  = true;
                           a.isPlay   = true;
                           this.musicObj = a;
                           this.$store.dispatch('gaiMusic',this.musicObj);
                            
                           
                        // 判断是否单曲加入队列
                         if(isSingle){
                             let ispduan =  true;
                             this.queue.forEach((Item,index)=>{
                                 if(Item.songId == id){
                                       ispduan = false;
                                       this.queue.splice(index,1);
                                       this.queue.unshift({
                                            songId    : id,
                                            songName  : name,
                                            songUrl   : imgUrl,
                                            autName   : auto,
                                            songBrief : Brief,
                                        })

                                 }
                             });
                            if(ispduan){
                                this.queue.unshift({
                                    songId    : id,
                                    songName  : name,
                                    songUrl   : imgUrl,
                                    autName   : auto,
                                    songBrief : Brief,
                                })
                               
                                let a =  this.queue.slice(0,30);
                                this.$store.dispatch('gaiQueue', a)

                            }
                           
                             sessionStorage.setItem('queue',JSON.stringify(this.queue))
                             this.isSingle = false;

                         }
                           
                          
                           
                     }
                 })
                 .catch(err=>{
                     console.log(err)
                 })

                },1000)
                 console.log(id,name,imgUrl)
                
            },
            // 改播放队列
            ff_queue(){
                this.$toast('已加载到播放队列')
                let ques = [];

                if(this.newList.length >=30){
                     for(let i = 0;i < 30;i++){
                        ques.push(this.newList[i])
                     }

                }else{
                    for(let i = 0;i < this.newList.length;i++){
                         ques.push(this.newList[i])
                     }
                }
               
                this.$store.dispatch('gaiQueue', ques).then(res=>{
                     this.ff_songs(res[this.queueIndex].songId,res[this.queueIndex].songName,res[this.queueIndex].songUrl); 
                })
            },
            // 跳转页面
            goPack(name){
                this.$router.push({name})
            }
        },
        computed:{
            ...mapState({
                 music      : 'song',
                 queue      : 'queue',
                 queueIndex : 'queueIndex',
            })
        },

      
        components:{
            Flash
        }
       
        
     
    }
</script>

<style lang="less" scoped>
  
</style>